.bt {
    width: 25%;
    margin: 5% 0 0 5%;
    cursor: pointer;
    position: relative;
    font-family: "Roboto";
    text-transform: uppercase;
    color: #2ecc71;
    letter-spacing: 0.5px;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    outline: none;
    text-decoration: none;
    text-align: center;
  
}

.more-bt {
    border-right: 2px solid #2ecc71;
    border-bottom: 2px solid #2ecc71;
    padding: 17px 29px 15px 31px;
    border-color: #2ecc71;
}

.more-bt {
  font-size: 1em;
}

#wrapper.smooth section.smoothy.show {
    visibility: visible;
}

.more-bt:before {
    left: 0;
    bottom: 0;
    height: calc(100% - 17px);
    width: 2px;
}

.more-bt:after, .more-bt:before {
    content: " ";
    display: block;
    background: #2ecc71;
    position: absolute;
    transition: .5s;
    z-index: 10;
}

.more-bt:after {
    top: 0;
    right: 0;
